import React from "react";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Typography from "@mui/material/Typography";
import { CardActionArea } from "@mui/material";

export default function MultiActionAreaCard() {
  return (
    <Card sx={({ maxWidth: 1 }, { zIndex: "tooltip" })}>
      <CardActionArea>
        <CardMedia
          component="img"
          height="100%"
          image="https://cdn6.bigcommerce.com/s-0kvv9/images/stencil/500x659/products/42815/36181/pokemon-card-game-base-single-card-rare-holo-4-charizard-20__72610.1461051559.jpg?c=2"
        />
        <CardContent>
          <Typography gutterBottom variant="h3" component="div">
            2位:海外版 リザードン
          </Typography>
          <Typography variant="h5" color="text.secondary" component="div">
            [相場価格]　3000万円以上
          </Typography>
          <Typography variant="h5" color="text.secondary" component="div">
            [入手方法]　海外版の拡張パック・ベースセットのレアカードを入手する
          </Typography>
        </CardContent>
      </CardActionArea>
    </Card>
  );
}
