import MediaQuery from "react-responsive";
import PC from "./pc_rekishi";
import PHONE from "./phone_rekishi";
import TAB from "./tab_rekishi";

export default function Intro() {
  return (
    <div clasName="intro">
      <MediaQuery query="(max-width:600px)">
        <PHONE />
      </MediaQuery>
      <MediaQuery query="(min-width:600px) and (max-width:950px)">
        <TAB />
      </MediaQuery>
      <MediaQuery query="(min-width:950px)">
        <PC />
      </MediaQuery>
    </div>
  );
}
