import MediaQuery from "react-responsive";
import PC from "./pc_rank";
import PHONE from "./phone_rank";
import TAB from "./tab_rank";
export default function App() {
  return (
    <div clasName="Rank">
      <MediaQuery query="(max-width:600px)">
        <PHONE />
      </MediaQuery>
      <MediaQuery query="(min-width:600px) and (max-width:950px)">
        <TAB />
      </MediaQuery>
      <MediaQuery query="(min-width:950px)">
        <PC />
      </MediaQuery>
    </div>
  );
}
